    background-position: left bottom;
    background-repeat: repeat-x;
    left: 0;
    margin-bottom: -5px;
    height: 50vh;
    min-height: 50vh;
    width: 100vw; }
  .header .header-logo {
    width: 250px;
    z-index: 99; }
