    position: absolute;
    bottom: 30px;
    left: auto;
    right: auto;
    z-index: 99;
    text-rendering: auto;
    color: #E9275B;
    font-size: 33px;
    background: none;
    border: none;
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    text-align: center;
    cursor: pointer;
    -webkit-transition: 0.3s all;
    transition: 0.3s all; }
